$font-family-sans-serif: "游ゴシック", YuGothic, "游ゴシック体", "Yu Gothic", "メイリオ", Meiryo, "ＭＳ Ｐゴシック", "MS PGothic", Robot, "Droid Sans", sans-serif;
$font-family-serif: "Times New Roman", "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "ＭＳ Ｐ明朝", "MS PMincho", serif;
$font-family-monospace: "Source Code Pro", "Osaka－等幅", Osaka-mono, "ＭＳ ゴシック", "MS Gothic", monospace;

$font-size-base: 14px;

$line-height-base: 1.428571429;

$headings-font-family: inherit;
$headings-font-weight: 500;
$headings-line-height: 1.1;
$headings-color: inherit;

@mixin family($family: normal) {
	font: {
		@if $family == normal {
			family: $font-family-sans-serif;
		} @else if $family == gosick {
			family: $font-family-monospace;
		} @else if $family == mincho {
			family: $font-family-serif;
		} @else {
			family: sans-serif;
		}
	}
}


