.entry-row.has-blocks {
  .post-sidebar {
    max-width: 72rem;
    margin: 6rem auto;
    padding: 0 1.6rem;
    column-count: 1;
    column-gap: 2rem;

    @media (min-width: $md-min) {
      column-count: 2;
    }

    .widget {
      page-break-inside: avoid;
      break-inside: avoid;
    }
  }
}

.entry-row:not(.has-blocks) {
  .post-sidebar {
    margin-top: 6em;
    column-count: 1;
    column-gap: 2rem;

    @media (min-width: 768px) {
      column-count: 2;
    }

    @media (min-width: 1200px) {
      margin-top: 0;
      column-count: 1;
    }

    .widget {
      page-break-inside: avoid;
      break-inside: avoid;
    }
  }
}

.entry-header,
.entry-footer {
  max-width: 72rem;
}
.entry-header {
  margin: 0 auto 4rem;

  .title {
    margin: 0 0 1rem;
    font-size: 2.4rem;

    @media (min-width: $md-min) {
      font-size: 3rem;
    }
  }

  &__row {
    @media (min-width: $md-min) {
      display: flex;
      justify-content: space-between;
    }
  }
}
.entry-footer {
  margin: 4rem auto 0;
}
.entry-row.has-blocks {
  .entry-content {
    > * {
      margin-left: auto;
      margin-right: auto;
    }
  }
}
.entry-content {
  line-height: 1.5;
  // font-size: 1.4rem;
  // max-width: 520px;
  // margin: 0 auto;

  > * {
    margin-bottom: 1.25em;

    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }

  h1,h2,h3,h4,h5,h6 {
    margin-top: 4rem;
    font-weight: bold;

    &:first-child {
      margin-top: 0;
    }
  }
  h1 {
    font-size: 3rem;
  }
  h2 {
    font-size: 2.6rem;
  }
  h3 {
    font-size: 2.2rem;
  }
  h4, h5, h6 {
    font-size: 2rem;
  }

  > ol:not(.wp-block-gallery),
  > ul:not(.wp-block-gallery) {
    padding-left: 2rem;

    > li {
      margin-bottom: 1rem;
    }
    ol,
    ul {
      margin-top: 1rem;
      padding-left: 2rem;
    }
  }
}

.entry-content > *:not(.alignwide):not(.alignfull):not(.alignleft):not(.alignright):not(.is-style-wide):not(.wp-block-separator) {
  max-width: 72rem;
  width: calc(100% - 4rem);
}

[class*="__inner-container"] > *:not(.alignwide):not(.alignfull):not(.alignleft):not(.alignright):not(.is-style-wide) {
  max-width: 72rem;
  width: 100%;
}


/* alignment */
.alignnone,
.aligncenter,
.alignleft,
.alignright,
.alignwide {
  margin-top: 4rem;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 4rem;
}

[class*="__inner-container"] > *:not(.alignwide):not(.alignfull):not(.alignleft):not(.alignright) {
  margin-left: auto;
  margin-right: auto;
}


/* Full */
.alignfull {
  position: relative;
  max-width: 100%;
  width: 100%;
  margin-top: 5rem;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 5rem;
}

[class*="__inner-container"] > .alignfull {
  max-width: 100%;
}


/* Wide */
.alignwide {
  position: relative;
  max-width: 120rem;
  width: calc(100% - 4rem);
}

[class*="__inner-container"] > .alignwide {
  width: 100%;
}

/* Center */
.aligncenter,
.aligncenter img {
  margin-left: auto;
  margin-right: auto;
}

/* Left and right */
.alignleft,
.alignright {
  max-width: 50%;
}

.alignleft {
  /*rtl:ignore*/
  float: left;
  margin: 0.3em 2rem 2rem 2rem;
}
.alignright {
  float: right;
  margin: 0.3em 2rem 2rem 2rem;
}

[class*="wp-block"].alignfull:not(.has-background) [class*="__inner-container"] > .alignleft,
[class*="wp-block"].alignfull:not(.has-background) [class*="__inner-container"] > p .alignleft,
[class*="wp-block"].alignfull:not(.has-background) [class*="__inner-container"] > .wp-block-image .alignleft {
  margin-left: 2rem;
}

[class*="wp-block"].alignfull:not(.has-background) [class*="__inner-container"] > .alignright,
[class*="wp-block"].alignfull:not(.has-background) [class*="__inner-container"] > p .alignright,
[class*="wp-block"].alignfull:not(.has-background) [class*="__inner-container"] > .wp-block-image .alignright {
  margin-right: 2rem;
}

/* Entry Media */
.alignfull > figcaption,
.alignfull > .wp-caption-text {
  max-width: 72rem;
  width: calc(100% - 4rem);
  margin-left: auto;
  margin-right: auto;
}



/* Separator */
.wp-block-separator {
  margin-top: 4rem;
  margin-bottom: 4rem;
}


/* Preformatted */
.wp-block-preformatted {
  padding: 1.4rem;
  font-family: $font-family-monospace;
  background-color: #f0f0f0;
}


/* Verse */
.wp-block-verse {}


/* Table */
.wp-block-table {}


/* Latest Posts */
.wp-block-latest-posts {
  padding: 0;
}

.wp-block-latest-posts__list {
  list-style: none;
}


/* Blockquote */
.wp-block-quote {
  margin-top: 3rem;
  margin-bottom: 3rem;
  padding: 1.4rem;
  padding-left: 2rem;

  &.is-large {
    max-width: 100% !important;
    margin: 0 auto 2rem;
    padding: 1.4rem 2rem;

    p {
      font-size: 2rem;
    }

    cite {
      font-size: 0.9em;
    }
  }

}


/* Audio */
.wp-block-audio {}


/* Button */
.wp-block-button {}


/* Categories */
.wp-block-categories {}


/* Archives */
.wp-block-archives {}

.wp-block-archives-list {}


/* Columns */
.wp-block-columns {}


/* Pull Quotes */
.wp-block-pullquote {}


/* Image */
.wp-block-image {
  &.alignfull,
  &.alignwide {
    margin-top: 2rem;
  }
}


/* Embed */
.wp-block-embed {
  &.alignfull,
  &.alignwide {
    margin-top: 2rem;
  }
}

